import React from 'react'
import ProductList from './products'
import Favourites from './favourites'
import Wishlist from './wishlist'


export default function store() {
 
  return (
      <div className='store'>
      <ProductList />
      <div>
        <div><Favourites/></div>
        <div><Wishlist/></div>
      </div>
    </div>
  )
}
